.App {
  text-align: center;
  height: 100vh;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(241, 241, 241);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  position: relative;
  top: 0;
  height: 60px;
  align-items: center;
  background: #3d3f4d65;
  width: 100%;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.45);
}

.header-title {
  margin: 0;
  margin-left: 40px;
  text-align: center; 
  font-size: 1.1rem;
  text-transform: uppercase;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main {
  display: flex;
  gap: 5rem;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}


.card-face-container {
  padding: 15px;
}

h1 {
  font-size: 1.3em;
  margin-top: 0;
}

.control-panel {
  position: fixed;
  max-height: 100%;
  right: 2rem;
  margin: auto 0;
}

.pseudo-control-panel {
  max-width: 150px;
  max-height: 230px;
  min-width: 150px;
  min-height: 230px;
}

.control-box {
  margin-top: 20px;
  padding: 10px;
  background-color: #3d3f4d65; 
  border: 1px solid #757575a2; 
  border-radius: 5px; 
  max-width: 150px;
  max-height: 230px;
  min-width: 150px;
  min-height: fit-content;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.45);
}

.control-item {
  margin: 10px 0; 
  display: flex;
  align-items: center; 
  justify-content: space-between; 
  max-width: 100%;
  max-height: 100%;
}

.switch {
  display: flex;
  align-items: center;
}

.switch input {
  display: none;
}

.switch-label {
  display: flex; 
  align-items: center;
  text-align: center;
  justify-content: center;
  display: block;
  width: 60px;
  height: 17px;
  background: #ccc;
  border-radius: 20px;
  position: relative;
  cursor: pointer;
  font-size: .6em;
  color: #000;
  background: #c5c5c5;
  max-width: none;
  min-width: none;
}

.switch-label:before {
  content: '';
  position: absolute;
  width: 13px;
  height: 13px;
  background: white;
  border-radius: 50%;
  top: 2px;
  left: 2px;
  transition: 0.3s;
}

.switch input:checked + .switch-label {
  background: #5d5f68bb; 
  color: #ffffff;
}

.switch input:checked + .switch-label:before {
  transform: translateX(43px); 
}

input {
  margin-left: 10px;
  width: 45px;
  max-width: 100%;
  max-height: 100%;
  height: 5px;
  appearance: none;
  border-radius: 5px;
  cursor: pointer;
}

.checkbox-input {
  width: 12px;
  height: 12px;
  appearance: auto;
}

label {
  font-size: .6em;
  text-align: left;
  max-width: 50px;
  min-width: 50px;
}

span {
  font-size: .6em;
  max-width: 35px;
  min-width: 35px;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none; 
  appearance: none;
  width: 10px;
  height: 10px; 
  border-radius: 50%; 
  background: #5d5f68; 
  cursor: pointer; 
}

input[type="range"]::-moz-range-thumb {
  width: 10px; 
  height: 10px; 
  border-radius: 50%; 
  background: #5d5f68;
  cursor: pointer; 
}

input[type="range"]::-ms-thumb {
  width: 10px;
  height: 10px;
  border-radius: 50%; 
  background: #5d5f68; 
  cursor: pointer; 
}

p {
  font-size: .6em;
}

h5 {
  margin: 10px auto;
}